import React from "react";
import styles from "./Users.module.css";
import userPhoto from "../../assets/images/ava1.png";
import {NavLink} from "react-router-dom";
import {usersAPI} from "../../api/api";
import {Button} from "@material-ui/core";

let Users = (props) => {
    return (
        <div className={styles.wraperOne}>
            <div className={styles.wraperTwo}>
                {props.users.map(u => <div key={u.id}  >
                    <div className={styles.wraperTree}>
                       <div>
                        <NavLink to={'/profile/' + u.id}>
                         <img src={u.photos.small != null ? u.photos.small : userPhoto} className={styles.userPhoto}
                           alt={' '}/>
                        </NavLink>
                       </div>

                        <div className={styles.button}>
                {u.followed ? <Button style={{
                        maxWidth: '5.8rem',
                        maxHeight: '1.5rem',
                        minWidth: '2.4rem',
                        minHeight: '1rem',
                        fontSize: '0.5rem',
                        textAlign: 'center'
                    }} color="primary"   variant="contained" disabled={props.followingInProgress.some(id => id === u.id)}
                                      onClick={() => {
                                          props.toggleFollowingProgress(true, u.id);
                                          return usersAPI.deleteUsers(u.id).then(data => {
                                              if (data.resultCode === 0) {
                                                  props.toggleFollow(u.id)
                                              }
                                              props.toggleFollowingProgress(false, u.id);
                                          });

                                      }}> Unfollow </Button>
                    : <Button style={{
                        maxWidth: '5.8rem',
                        maxHeight: '1.5rem',
                        minWidth: '2.4rem',
                        minHeight: '1rem',
                        fontSize: '0.5rem',
                        textAlign: 'center'
                    }}  color="primary"  variant="contained" disabled={props.followingInProgress.some(id => id === u.id)}
                              onClick={() => {
                                  props.toggleFollowingProgress(true, u.id);
                                  usersAPI.postUsers(u.id).then(data => {
                                      if (data.resultCode === 0) {
                                          props.toggleFollow(u.id)
                                      }
                                      props.toggleFollowingProgress(false, u.id);
                                  });

                               }}> Follow </Button>}
               </div>
<div className={styles.wraperfoo}>
                    <div>Name: {u.name} </div>
                    <div>Status: {u.status} </div>
                    <div>{"Location.country"}</div>
                    <div>{"Location.city"}</div>
</div>
                </div>
                </div>)
                }
            </div>
        </div>

    )
}
export default Users;
